@charset "UTF-8";
@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/Fira_Sans/FiraSans-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Fira Sans";
  src: url("../fonts/Fira_Sans/FiraSans-Medium.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

*::before,
*::after {
  box-sizing: inherit; }

body {
  margin: 0;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  color: #484848;
  box-sizing: border-box;
  position: relative; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  width: 1024px;
  margin: 0 auto;
  box-sizing: border-box; }

@media (max-width: 768px) {
  .container {
    width: 768px;
    padding: 0 50px; } }

@media (max-width: 320px) {
  .container {
    width: 320px; } }

.page-header {
  padding-top: 32px;
  padding-bottom: 826px;
  position: relative;
  background-image: url("../../img/mask.png");
  background-repeat: no-repeat;
  background-position: center 0;
  box-sizing: border-box; }
  .page-header::after {
    position: absolute;
    left: 515px;
    top: 169px;
    content: "";
    width: 381px;
    height: 609px;
    background: url("../../img/photo_girl.png") no-repeat;
    z-index: 1; }

.page-header__logo {
  position: absolute;
  top: 32px;
  left: 50%; }

.page-header__nav {
  width: 1024px;
  margin: 0 auto;
  position: relative; }
  .page-header__nav::after {
    content: "";
    width: 1018px;
    height: 340px;
    position: absolute;
    background: url("../../img/Израиль.png") no-repeat; }

.page-header__list {
  padding: 0;
  display: flex;
  justify-content: space-between;
  list-style: none;
  color: #484848; }

.page-header__item--tel {
  color: #484848; }

.page-header__item--call {
  border-bottom: 1px dashed #484848; }

.section-titles {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 49px;
  text-transform: uppercase;
  color: #1FC0FC; }

.site-title {
  width: 343px;
  margin: 0 auto;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #fff;
  text-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12); }

.about-program {
  margin-bottom: 56px; }

.about-program__title {
  width: 327px;
  margin: 0 auto 24px; }

.about-program__description {
  width: 720px;
  margin: 0 auto;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 170%;
  /* or 29px */
  color: #484848; }

.about-program__wrapper {
  padding: 48px 248px 96px 248px;
  background: url("../../img/watercolor_Bg.png") no-repeat;
  background-position: center 0; }

.about-program__img {
  margin: 0 auto; }

.programs {
  padding-top: 104px;
  padding-left: 210px;
  display: flex;
  flex-wrap: wrap;
  background: url("../../img/sketch.png") no-repeat;
  background-position: top right; }

.programs__title {
  width: 100%;
  margin: 0 0 83px; }

.programs__control {
  width: 230px;
  margin-right: 64px;
  display: flex;
  flex-wrap: wrap; }

.programs__label {
  width: 210px;
  padding: 25px 41px 24px 72px;
  position: relative;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 18px; }

.programs__control svg {
  position: absolute;
  top: 16px;
  left: 20px;
  fill: #000000; }

.programs__current-label {
  font-weight: 500;
  color: #ffffff;
  line-height: 19px;
  background: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%);
  position: relative; }
  .programs__current-label::before {
    content: "";
    width: 50px;
    height: 30px;
    position: absolute;
    right: -18px;
    top: -8px;
    background-color: #fff;
    transform: rotate(45deg); }
  .programs__current-label::after {
    content: "";
    width: 50px;
    height: 30px;
    position: absolute;
    right: -18px;
    bottom: -8px;
    background-color: #fff;
    transform: rotate(135deg); }

.programs__list {
  width: 600px;
  margin: 0;
  padding: 0; }

.programs__item {
  display: none; }
  .programs__item--current {
    display: block; }

.programs_item--title {
  text-transform: none;
  font-size: 28px;
  line-height: 34px;
  margin: 0;
  margin-bottom: 24px; }

.programs__item--description {
  line-height: 26px; }

.order {
  position: relative; }
  .order::before {
    content: "";
    width: 167px;
    height: 226px;
    position: absolute;
    top: 20px;
    left: -14px;
    background: url("../..//img/backpack.png") no-repeat;
    transform: rotate(15deg); }
  .order::after {
    content: "";
    width: 818px;
    height: 594px;
    position: absolute;
    right: 100px;
    top: -210px;
    background: url("../../img/girl-flag-bg.png") no-repeat; }

.order__title {
  margin-left: 210px;
  margin-top: 210px; }

.order__text {
  width: 400px;
  margin-left: 210px;
  margin-bottom: 64px;
  font-size: 17px;
  line-height: 170%; }

.order__user-tel {
  width: 261px;
  margin-left: 210px;
  padding: 19px 145px 20px 40px;
  border: 2px solid #E3E3E3;
  box-sizing: border-box;
  border-radius: 80px; }
  .order__user-tel::placeholder {
    font-weight: normal;
    font-size: 17px;
    line-height: 20px;
    font-variant: small-caps;
    opacity: 0.5; }

.order__submit {
  position: absolute;
  padding: 19px 40px 20px;
  width: 216px;
  left: 380px;
  top: 203px;
  background: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%);
  border-radius: 80px;
  text-transform: uppercase;
  color: #ffffff;
  z-index: 5; }
